<template>
    <div class="container">
        <h2>工时计算器</h2>
        <div class="check_container">
            <label>
                <input type="checkbox" v-model="showJsonInput" />
                通过Json计算
            </label>
            <label>
                <input type="checkbox" v-model="showFileInput">
                通过Excel计算
            </label>
        </div>
        <div v-if="showFileInput" class="fileupload">
            <ul>
                <li>
                    使用方法:
                </li>
                <li>
                    1. 打开考勤日历
                </li>
                <li>
                    2. 从序号、编号这一栏开始向下全选复制
                </li>
                <li>
                    3. 打开Excel,选中一格,依次点击右键->选择性粘贴->选择性粘贴->文本,即可将文本保存到Excel中,然后上传Excel文件即可
                </li>
            </ul>
            <input type="file" ref="fileInput" accept=".xls,.xlsx" @change="onFileChange">
        </div>
        <div v-else-if="showJsonInput" class="jsoninput">
            <ul>
                <li>
                    使用方法:
                </li>
                <li>
                    1. 打开考勤日历
                </li>
                <li>
                    2. 选择在浏览器打开
                </li>
                <li>
                    3. 在浏览器中右键选择检查，选择网络网络，然后刷新界面
                </li>
                <li>
                    4. 找到kmReviewCardRecord.do?method=data接口返回的值，全选拷贝到输入框中即可
                </li>
            </ul>
            <textarea v-model="jsonString" rows="10" cols="50"></textarea>
        </div>

        <button @click="upload">计算工时</button>
        <ul>
            <li class="item" v-for="info in data" :key="info">{{ info }}</li>
        </ul>
    </div>
</template>
  
<script>
import axios from 'axios';
import host from '../config/hosts.js'

export default {
    data() {
        return {
            file: null,
            uploading: false,
            progress: 0,
            fileCode: null,
            data: null,
            showJsonInput: true,
            showFileInput: false,
            jsonString: null,
            jsonObj: null,
        };
    },
    watch: {
        showJsonInput(newVal) {
            if (newVal) {
                this.showFileInput = false;
            }
        },
        showFileInput(newVal) {
            if (newVal) {
                this.showJsonInput = false;
            }
        }
    },
    methods: {
        onFileChange(event) {
            this.file = event.target.files[0];
        },
        upload() {

            if (this.showJsonInput) {
                if (!this.jsonString) {
                    alert("请输入json数据")
                    return
                }
                try {
                    JSON.parse(this.jsonString);
                } catch (error) {
                    this.parsedData = null;
                    alert("json数据格式不正确")
                    return
                }
                this.uploadJson()
            }

            if (this.showFileInput) {
                if (!this.file) {
                    alert('请选择文件');
                    return;
                }
                this.uploadFile()
            }
        },

        uploadJson() {
            const jsonInfo = {
                info: this.jsonString
            }

            axios
                .post(host.url + '/uploadjson', jsonInfo, {
                    headers: {
                        'Content-Type': 'application/json',
                    }
                })
                .then((response) => {
                    this.data = response.data.data
                    this.jsonString = null
                    console.log(response)
                })
                .catch((error) => {
                    console.error(error);
                });
        },

        uploadFile() {
            // 创建一个FormData对象来发送文件
            const formData = new FormData();
            formData.append('file', this.file);

            this.uploading = true;
            this.progress = 0;

            axios
                .post(host.url + '/uploadfile', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    }
                })
                .then((response) => {
                    this.fileCode = response.data.data
                    console.log(response)
                    this.getworktime()
                })
                .catch((error) => {
                    alert('上传失败');
                    console.error(error);
                });
        },

        getworktime() {
            axios
                .get(host.url + '/worktime', {
                    params: {
                        'fileId': this.fileCode
                    }
                })
                .then((response) => {
                    this.data = response.data.data
                    console.log(response)
                    this.deleteFile()
                })
                .catch((error) => {
                    console.error(error);
                    this.deleteFile()
                });
        },
        deleteFile() {
            axios
                .get(host.url + '/delete', {
                    params: {
                        'fileId': this.fileCode
                    }
                })
                .then((response) => {
                    console.log(response)
                    this.file = null
                    this.fileCode = null
                })
                .catch((error) => {
                    console.error(error);
                });
        }
    },
};
</script>

