import Vuex from "vuex";

export default new Vuex.Store({
  state: {
    isLoggedIn: false,
  },
  mutations: {
    login(state) {
      state.isLoggedIn = true;
      console.log('login')
    },
    logout(state) {
      state.isLoggedIn = false;
      console.log('logout')
    },
  },
  actions: {
    login({ commit }) {
      commit("login");
    },
    logout({ commit }) {
      commit("logout");
    },
  },
});
