<template>
    <div>
        <h2>京东cookie获取</h2>
        <ul>
            <li>
                使用方法:
            </li>
            <li>
                1. 登陆京东网站，进入购物车页面
            </li>
            <li>
                2. 在浏览器中右键选择检查，选择网络，然后点击购物车全选按钮
            </li>
            <li>
                3. 选中api?functionId=pcCart_jc_cartCheckAll&appid=JDC_mall_cart接口
            </li>
            <li>
                4. 切换到标头tab,然后拷贝Cookie字段的值,粘贴到cookie输入框
            </li>
            <li>
                5. 切换到载荷tab,然后拷贝body字段的值,粘贴到body输入框
            </li>
            <li>
                6. 提交信息
            </li>
        </ul>
        <div>
            <div class="input_cookie">
                <span>cookie输入框</span>
                <textarea v-model="cookieString" rows="3" cols="50"></textarea>
            </div>
            <div class="input_body">
                <span>body输入框</span>
                <textarea v-model="bodyString" rows="3" cols="50"></textarea>
            </div>
        </div>
        <div class="submit_info">
            <span class="btn_span" @click="uploadCookie">上传信息</span>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import host from '../config/hosts.js'

export default {
    data() {
        return {
            cookieString: "",
            bodyString: ""
        }
    },
    methods: {
        uploadCookie() {
            console.log("uploadCookie")
            if (!this.cookieString || this.cookieString.trim().length == 0) {
                alert("请输入cookie数据")
                return
            }

            if (!this.bodyString || this.bodyString.trim().length == 0) {
                alert("请输入body数据")
                return
            }
            const cookieInfo = {
                cookie: this.cookieString,
                reqBody: this.bodyString.replace(/\n/g, '')
            }

            console.log(cookieInfo)

            axios.post(host.url + "/uploadJDCookie", cookieInfo, {
                headers: {
                    'Content-Type': 'application/json',
                }
            }).then((response) => {
                console.log(response)
                alert("上传成功")
            }).catch((error) => {
                console.log(error)
            })
        }
    }
}

</script>

<style>
.input_cookie {
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
    align-items: flex-start;
}

.input_body {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.submit_info {
    display: flex;
    align-items: flex-start;
    margin-top: 30px;
    height: 50px;
}

.btn_span {
    align-self: center;
    background-color: aquamarine;
    padding: 10px;
    border-radius: 5px;
}
</style>